.imgDiv{
    margin-left: 3rem;
}

.btnStyl{
    width: 12em; 
    margin-right:2rem;
    font-size: 0.875rem; 
    font-weight: 500;
}
.cancelBtn{
    cursor: pointer;
    font-size: 1.5rem;
    color: gray;
    position: relative;
    top: -1.5rem;
    left: 1rem;
}
.cancelBtn:hover{
    color: blue;
}
.upload-section {
    min-width: 220px;
  }
  .dotted-boundary {
    position: relative;
    padding: 16px 40px;
    border: 1px solid rgb(185, 179, 179);
    border-radius: 10px;
    border-style: dashed;
  }
  
  .upload-img {
    text-align: center;
  }
  .upload-text {
    text-align: center;
    color: var(--link-color);
  }
  .upload-buttons {
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    text-align: center;
    top: 10%;
    opacity: 0;
  }
  .upload-buttons .p-button {
    padding: 0.5rem 1rem;
    border-radius: 5px;
  }

  .borderClass {
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-radius: 0;
}

.editBtn {
  cursor: pointer;
  margin-left: 1.5rem;
  font-size: 1rem;
  color: gray;
}

.editBtn:hover {
  color: blue;
}